import { mapStringController } from "../Api";
import loadGoogleMapsApi from "load-google-maps-api";
import carregaMapsClass from './carregaMapsClass'
import firstUse from "../libs/firstUse";

export async function init(funn, phashh) {
  firstUse();
  const apiMapsString = await mapStringController();
  const eeeeee = new carregaMapsClass()
  loadGoogleMapsApi({ key: apiMapsString }).then(eeeeee.carregaMapa(phashh,funn));
}
