
import getUrlVal from "../libs/get_url_val";
import alreadyUsed from './alreadyUsed'
import Marker from "./Marker";
import { echoClient } from "../Api";

export default class carregaMapsClass {

  carregaMapa(hash, funcao) {
    return function (googleMaps) {
      let markerInstancia, map;
      const latLongZoom = new getUrlVal(["lat", "long", "zooml"]);
      let { lat, long, zooml } = latLongZoom.get_list();

      const { latitude, longitude, zoom } = alreadyUsed(lat, long, zooml)
      markerInstancia = new Marker(googleMaps, latitude, longitude, zoom);
      map = markerInstancia.getMap();
      MontaDados();

      map.addListener('zoom_changed', MontaDados)


      map.addListener("drag", MontaDados);
      async function MontaDados() {
        localStorage.setItem("lat", map.getCenter().lat());
        localStorage.setItem("long", map.getCenter().lng());
        localStorage.setItem("zoom", map.getZoom());
        document.getElementById("lat").value = map.getCenter().lat().toFixed(6);
        document.getElementById("long").value = map.getCenter().lng().toFixed(6);
        document.getElementById("zoom").value = map.getZoom();
        const link = `${window.location}?lat=${map.getCenter().lat()}&long=${map.getCenter().lng()}&zooml=${map.getZoom()}`;
        document.getElementById("link").value = link

        const centro = markerInstancia.centerMap(8, 2);
        if (!centro) return;

        const stringHash = await hash;
        const retornoApiPontos = await markerInstancia.getPontos(centro, stringHash);
        // console.log(retornoApiPontos)

        if (retornoApiPontos.data.status) {
          alert("cliente não cadastrado");
          return;
        }
        markerInstancia.criarIcones(retornoApiPontos.data.pontos, funcao);
      }
      document.getElementById("control").addEventListener("click", botaoApertado);
      function botaoApertado() {
        var latLng = markerInstancia.latLong(
          document.getElementById("lat").value,
          document.getElementById("long").value
        );
        map.panTo(latLng);
        map.setZoom(Number(document.getElementById("zoom").value));
        MontaDados();
      }

      document
        .getElementById("controll")
        .addEventListener("click", botaoApertado2);
      async function botaoApertado2() {
        const tokenToSend = await hash;
        await echoClient(tokenToSend);
      }
    }
  }
}