import React, { Component } from "react";
import { fullpage_index } from "./Api";
import { Page } from "./libs/page";
import MyCanvas from "./MyCanvas";
import CustomIFrame from "./CustomIFrame";
import Img from "./CustomImg";
import "./css/App.css";
import "./css/btn.css"

class App extends Component {
  state = {
    pagina: false,
    concessionaria_pagina: 0,
    contatos: [],
    STREET: [],
    foto: [],
    WIKIMAPIA_SAT: [],
    WIKIMAPIA_FRIO: [],
    pm: [],
    site: [],
    abcr: [],
    concessionaria: [],
    FOTO_MAPA: [],
    WIKIPEDIA: [],
    LEI: [],
  };



  topFunction = function () {
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
  }

  async componentDidMount() {
    var mybutton = document.getElementById("myBtn");
    window.onscroll = function () { scrollFunction() };

    function scrollFunction() {
      if (document.body.scrollTop > 20 || document.documentElement.scrollTop > 20) {
        mybutton.style.display = "block";
      } else {
        mybutton.style.display = "none";
      }
    }

    const { id_pagina, tes } = this.props

    const v = await id_pagina
    const u = await tes
    const dados = await fullpage_index(v, u);
    const { nome_pagina } = dados.data.dados

    try {
      if (!nome_pagina) return;
      const page = new Page(dados);
      const { pagina, concessionaria_pagina, contatos, obj } = page.get_infos();
      document.title = `BAHM - ${pagina.nome_pagina}`;
      if (contatos !== undefined) this.setState({ contatos });

      const {
        STREET,
        LEI,
        FOTO_MAPA,
        FOTO,
        WIKIMAPIA_FRIO,
        WIKIMAPIA_SAT,
        WIKIPEDIA,
        PM,
        SITE,
        ABCR,
        CONCESSIONARIA,
      } = obj;

      this.setState({
        pagina,
        concessionaria_pagina,
        STREET: STREET ? STREET : [],
        LEI: LEI ? LEI : [],
        FOTO_MAPA: FOTO_MAPA ? FOTO_MAPA : [],
        WIKIMAPIA_FRIO: WIKIMAPIA_FRIO ? WIKIMAPIA_FRIO : [],
        WIKIMAPIA_SAT: WIKIMAPIA_SAT ? WIKIMAPIA_SAT : [],
        WIKIPEDIA: WIKIPEDIA ? WIKIPEDIA : [],
        concessionaria: CONCESSIONARIA ? CONCESSIONARIA : [],
        pm: PM ? PM : [],
        site: SITE ? SITE : [],
        abcr: ABCR ? ABCR : [],
        foto: FOTO ? FOTO : [],
      });
    } catch (error) {
      console.log(error)
    }
  }

  render() {
    return (
      <>
        {<button onClick={() => {
          this.setState({
            pagina: false,
            concessionaria_pagina: 0,
            contatos: [],
            STREET: [],
            foto: [],
            WIKIMAPIA_SAT: [],
            WIKIMAPIA_FRIO: [],
            pm: [],
            site: [],
            abcr: [],
            concessionaria: [],
            FOTO_MAPA: [],
            WIKIPEDIA: [],
            LEI: []
          })

          this.props.fun(undefined)
        }}>VOLTAR</button>}
        {<div id="principal">

          {this.state.pagina && <MyCanvas dados={this.state} />}
          {[
            this.state.STREET,
            this.state.foto,
            this.state.WIKIMAPIA_SAT,
            this.state.WIKIMAPIA_FRIO,
            this.state.pm,
            this.state.site,
            this.state.abcr,
            this.state.concessionaria,
            this.state.FOTO_MAPA,
            this.state.LEI,
            this.state.WIKIPEDIA,
          ].map((data, index) => {
            // console.log(data,index)
            return data.map((data) => {
              if (index === 1 && data.indexOf('http') === -1)
              return (
                <div>
         
                  <br />
                  <br />
                  <br />
                  <Img key={data} dados={`${process.env['REACT_APP_BASE_URL2']}${data}`} />
                </div>
              );



              if (index === 8 && data.indexOf('http') === -1)
                return (
                  <div>
                    <br />
                    <br />
                    <br />
             
                    <Img key={data} dados={`${process.env['REACT_APP_BASE_URL2']}${data}`} />
                  </div>
                );


                if (index === 8)
                return (
                  <div>
                    <br />
                    <br />
                    <br />
             
                    <Img key={data} dados={data} />
                  </div>
                );




              return (
                <div>
                  <br />
                  <br />
                  <br />
                  <CustomIFrame key={data} dados={data} />
                </div>
              );
            });
          })}
        </div>
        }
        <button
          onClick={this.topFunction}
          id="myBtn" title="Go to top">Top</button>
      </>
    );
  }
}
export default App;
