import Axios from "axios";
let url
if (process.env['REACT_APP_BASE_URL'] !== 'window.location.origin')
  url = `${process.env['REACT_APP_BASE_URL']}`
else
  // url = `${eval(process.env['REACT_APP_BASE_URL'])}`
  url = `${window.location.origin}`

const api = Axios.create({
  baseURL: url,
});

export const fullpage_index = (value, user) =>
{
  const form = new FormData()
  form.append('id',value)
  form.append('token',user)
  return api.post('/FullpageController.php',form)
}


export const echoClient = (token)=> {
  const form = new FormData()
  form.append('token',token)
  return api.post('/echoClient.php',form)
}



export const mapStringController = () => fetch(`${url}/mapStringController.php`)
  .then(data => data.text());


export const CreateUserController = () => fetch(`${url}/CreateUserController.php`)
.then(data => data.text());


export const FullMaker = (form) => api.post(`/FullmarkerController.php`, form)


export const GenerateHash = () => {
  const form = new FormData()
  form.append('key', String(Math.random() * 10))
  const optReq = { method: 'post', body: form }
  const retorno = fetch(`${url}/GenerateHash.php`, optReq)
  .then(data => data.text())
  return retorno
}