export default (lat,long, zooml)=>{

    let latitude;
    let longitude;
    let zoom;

    if ((lat, long, zooml)) {
      latitude = lat;
      longitude = long;
      zoom = Number(zooml);
      window.location.search = "";
    } else {
      latitude = localStorage.getItem("lat");
      longitude = localStorage.getItem("long");
      zoom = Number(localStorage.getItem("zoom"));
    }


    return {
        latitude,
        longitude,
        zoom
    }
}