import React, { useState } from "react";
import Mapa from "./Mapa";
import Pagina from "./Pagina";
import verifyAndCreateHash from "./libs/verifyAndCreateHash";

export default () => {
  const [id_pagina, setId_pagina] = useState();
  let hash_id = verifyAndCreateHash();

  function fun(element) {
    setId_pagina(element);
  }

  function mapa() {
    if (!id_pagina) return <Mapa fun={fun} phash={hash_id} />;
  }

  function pagina() {
    if (id_pagina)
      return <Pagina tes={hash_id} id_pagina={id_pagina} fun={fun} />;
  }

  return (
    <>
      {mapa()}
      {pagina()}
    </>
  );
};
